import Vue from 'vue';
import App from './App.vue';
import Lang from 'vuejs-localization';

Vue.config.productionTip = true;

//Notice that you need to specify the lang folder, in this case './lang'
Lang.requireAll(require.context('./lang', true, /\.js$/));

Vue.use(Lang);

new Vue({
  render: h => h(App),
}).$mount('#app')
